h1, h2, h3, h4, h5, h6 {
  position: relative;
  line-height: 26px;
  margin: 18px 0;
  font-size: 18px;

  &:before {
    content: '';
  }
}

h1 {
  padding: 0 15px 0 15px;
  font-size: 22px;

  &:before {
    font-size: 22px;
    content: '¶';
    position: absolute;
    left: -5px;
    top: 0;
    color: #38bdf8;
  }
}

h2 {
  padding: 0 15px;
  font-size: 20px;

  &:before {
    position: absolute;
    display: block;
    top: 10%;
    bottom: 10%;
    left: 0;
    width: 4px;
    border-radius: 2px;
    background-color: #38bdf8;
  }
}

h3 {
  padding: 0 15px;

  &:before {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 4px;
    background-color: #38bdf8;
    border-radius: 0 4px 4px 0;
  }
}

h4 {
  margin-left: -10px;

  &:before {
    content: '「';
    font-weight: 600;
    margin-right: 5px;
    color: #38bdf8;
  }

  &:after {
    content: '」';
    font-weight: 600;
    margin-left: 5px;
    color: #38bdf8;
  }
}

h5 {
  padding: 0 15px;

  &::before {
    content: '';
    position: absolute;
    top: 2px;
    left: -8px;
    width: 20px;
    height: 20px;
    background-image: url('data:image/png;base64,');
    background-size: 100% 100%;
  }
}

h6 {
  padding: 0 15px;

  &::before {
    content: '';
    position: absolute;
    top: 2px;
    left: -8px;
    width: 20px;
    height: 20px;
    background-image: url('data:image/png;base64,');
    background-size: 100% 100%;
  }
}

hr {
  border-bottom: 2px solid #0ea5e9;
  margin: 16px auto;
}

p {
  line-height: 26px;
  margin: 0 0 16px;

  &:last-child {
    margin-bottom: 0;
  }
}

blockquote {
  margin: 0 auto 16px;
  padding: 8px 16px;
  border: 0;
  border-left: 4px solid #60a5fa;
  background-color: #eff6ff;
  color: #3b82f6;
  line-height: 26px;

  &:last-child {
    margin-bottom: 0;
  }

  p {
    margin: 0;
  }
}

a:not([class]) {
  line-height: 26px;
  color: #2563eb;
  text-decoration: none;
  transition: all .146s;

  &:hover {
    color: #0284c7;
    text-decoration: underline;
  }
}

ol {
  list-style: decimal;
  padding-left: 32px;
  margin-bottom: 16px;
}

ul {
  list-style: disc;
  padding-left: 32px;
  margin-bottom: 16px;
}

:not(pre) > code {
  display: inline-block;
  font-size: 14px;
  margin: 0 4px;
  padding: 0 4px;
  border-radius: 4px;
  white-space: pre-wrap;
  word-break: break-all;
  line-height: 20px;
  background-color: #f9fafB;
}

pre {
  position: relative;
  margin-bottom: 16px;
  font-size: 14px;
  border-radius: 4px;
  overflow: hidden;
  white-space: pre-wrap;
  background-color: #f9fafB;
  border: 1px solid #ccc;
  padding: 0;

  > code {
    position: relative;
    display: block;
    word-break: break-all;
    white-space: pre-wrap;

    &.hljs {
      padding: 32px 8px 8px;
    }
  }

  > code[class*="language-"] {
    background-color: transparent;

    &:before {
      content: '';
      position: absolute;
      left: 8px;
      top: 10px;
      height: 12px;
      width: 12px;
      border-radius: 50%;
      z-index: 1;
      background: #fc625d;
      box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;
    }
  }

  .copy {
    position: absolute;
    display: flex;
    top: 8px;
    right: 8px;
    width: 16px;
    height: 16px;
    cursor: pointer;
    fill: currentColor;
  }
}

table {
  caption-side: bottom;
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 1rem;
  vertical-align: top;

  thead {
    th {
      font-weight: 600;
    }
  }
}


@import "hljs";
@import "x";
